










































































































































































import service from '../../service.js';
// import sharebtn from '@/components/shareBox.vue';
export default {
	data() {
		return {
			id:0,
			datetime:'-',
			order_id:0,
			money:0,
			area:0,
			aname:'-',
			project_name:'-',
			address:'-',
			fmoney:0,
			head_img:'/static/missing-face.png',
			cuhead_img:'/static/missing-face.png',
			uid: 56,
			booking_time: "",
			pro_id: 6,
			type: 0,
			maketype: 0,
			clicknum: 0,
			time: 1559790973,
			remark: "",
			person: {
				"id": 2,
				"nickname": "",
				"mobile": "",
				"name": "",
				"addressurl": null,
				"addressinfo": null,
				"addresscode": "",
				"head_img": null
			},
			nickname: "",
			mobile: "",
			addressurl: "",
			addressinfo: "",
			my:'',
			com_id:'',
		}
	},
	filters:{
		formateDate(val){			
			const date = new Date(val);
			const year = date.getFullYear()
			const month = date.getMonth()>9?date.getMonth()+1:`0${date.getMonth()+1}`;
			const day = date.getDate()>9?date.getDate():`0${date.getDate()}`; 
			return `${year}-${month}-${day}`;
		}
	},
	onLoad(option) {
		if(option.id){
			this.id = option.id
		}else{
			uni.showToast({
				title:'分享数据过期，请重新分享'
			})
		}
	},
	mounted(){
			this.initData()
	},
	methods:{
		initData(){
			let self = this;		
			
			uni.request({
				url:service.BASEURL+'/home/share/high_seas_info',
				method:"POST",
				data:{
					id:this.id
				},
				success(res) {
					if(!res.data || res.data.code !=1){
							uni.showToast({
								title: res.data.msg,
								icon:'none'
							});
					}else{						
						let info = res.data.data;
						self.datetime = info.datetime;
						self.order_id = info.order_id;
						self.money = info.money;
						self.area = info.area;
						self.aname = info.aname;
						self.project_name = info.project_name;
						self.fmoney = info.fmoney;
						self.head_img = info.head_img;
						self.maketype = info.maketype;
						self.time = info.time;
						self.remark = info.remark;
						self.person = info.person;
						self.nickname = info.nickname;
						self.addressurl = info.addressurl;
						self.addressinfo = info.addressinfo;
						self.mobile = info.mobile;
						self.com_id = info.com_id;
					}
				}
			})
		},
		makeCall(){
			uni.makePhoneCall({
				phoneNumber:this.mobile
			})
		},
		showCompanyDetail(){
			uni.navigateTo({
				url:"/pages/companyinfo/companyinfo?id="+this.com_id,
			})
		},
		apply(){
			uni.showModal({
				title:'提示',
				content:"您需要下载App，进行接单",
				success(res){
					if(res.confirm){
						uni.showToast({
							title:'下载APP.....'
						})
					}
				}
			})
			
		}
	}
}
